import BreakagesModel from '../models/breakages';

export default async function updateBreakages(breakagesList) {
  const Breakages = BreakagesModel();
  await Promise.all(
    breakagesList.map(async (breakage) => {
      return Breakages.findByIdAndUpdate(
        { _id: breakage._id },
        breakage.update
      );
    })
  );

  return { message: 'Breakages updated successfully' };
}
